import Link from "next/link";

export default function ToggleForms({ isLoginPage }: { isLoginPage: boolean }) {
    return (
        <Link href={isLoginPage ? "/signup" : "/login"}>
            <div className="mt-6 text-center">
                <span>
                    {isLoginPage ? "Don't have an account?" : "Already Have An Account?"}
                    <button
                        className="w-[100px] h-10 ml-2 p-2 bg-gradient-to-r from-gray-300 
                            to-teal-400 text-white border rounded-lg cursor-pointer 
                            hover:from-orange-50 hover:to-pink-500"
                    >
                        {isLoginPage ? "Sign Up" : "Log In"}
                    </button>
                </span>
            </div>
        </Link>
    );
}
