"use client";

import { AddNewClientUser, auth, googleLogin, isCodeValid, storeGeneratedCode, useLogin } from "@/app/firebase";
import createNewClientUser from "@/app/firebase/authentication/createNewClientUser";
import { useUser } from "@/app/store/useUser";
import { sendVerificationEmail } from "@/app/utils";
import { usePathname } from "next/navigation";
import { useRouter } from "next/navigation";
import { useEffect, useState } from "react";
import { toast, Toaster } from "sonner";
import Form from "./Form";
import ThirdPartyAuth from "./ThirdPartyAuth";
import ToggleForms from "./ToggleForms";
import CodeModal from "./CodeModal";
import { useClient } from "@/app/store/useClient";

export default function AuthPages() {
    const router = useRouter();
    const [code, setCode] = useState("");
    const [email, setEmail] = useState("");
    const [password, setPassword] = useState("");
    const isLoginPage = usePathname() === "/login";
    const handleCloseModal = () => setShowCodeModal(false);
    const [confirmPassword, setConfirmPassword] = useState("");
    const [showCodeModal, setShowCodeModal] = useState<boolean | null>(null);
    const [passwordIsVisible, setPasswordIsVisible] = useState<boolean>(false);
    const onEmailChange = (event: React.ChangeEvent<HTMLInputElement>) => setEmail(event.target.value);
    const onPasswordChange = (event: React.ChangeEvent<HTMLInputElement>) => setPassword(event.target.value);
    const onConfirmPasswordChange = (event: React.ChangeEvent<HTMLInputElement>) => setConfirmPassword(event.target.value);
    const {
        email: userEmail,
        role,
        username,
        isVerified,
        setUserRole,
        setUserIsVerified,
        setUserEmail,
        setUserUsername,
        fetchUserData,
        user,
    } = useUser();

    const { initializeClient, isClient } = useClient();

    useEffect(() => {
        initializeClient();
    }, [initializeClient]);

    const handleSignupClick = async (event: React.FormEvent<HTMLFormElement>) => {
        event.preventDefault();
        if (!email || !password || !confirmPassword) {
            toast.error("Email, password, and confirmation are required.");
            return;
        }
        setShowCodeModal(true);
        const verificationResult = await sendVerificationEmail(email);
        console.log(verificationResult, "verification result");
        if (!verificationResult.success || !verificationResult.expirationDate) {
            toast.error(verificationResult.error);
            return;
        }
        const expirationDate = verificationResult.expirationDate;
        const verificationCode = verificationResult.code;
        const storeResult = await storeGeneratedCode(email, verificationCode, expirationDate);
        if (!storeResult.success) {
            toast.error(storeResult.error);
            return;
        }
    };

    useEffect(() => {
        const unsubscribe = useUser.getState().subscribeToAuthState();
        return () => unsubscribe();
    }, []);

    const handleVerify = async (e: any) => {
        e.preventDefault();
        const validCode = await isCodeValid(email, code);

        if (!validCode.success || !validCode.valid) {
            toast.error(validCode.error);
            return;
        }
        setCode("");
        setUserIsVerified(true);
        setShowCodeModal(false);

        const userCredentials = await createNewClientUser(email, password, confirmPassword);

        if (!userCredentials?.success || !userCredentials?.user) {
            toast.error(userCredentials?.error);
            return;
        }
        const uid = userCredentials.user.uid;
        const emailVerified = true;
        const displayName = userCredentials.user.displayName;

        const dbResult = await AddNewClientUser({
            uid,
            email,
            displayName,
            emailVerified,
            setUserRole,
            setUserIsVerified,
            setUserEmail,
            setUserUsername,
        });
        if (dbResult.success) router.push("/login");
    };

    const onGoogleIconClick = async () => {
        try {
            const result = await googleLogin();
            if (result.success && result.user) router.push("/");
            else if (result.error) throw Error(result.error);
        } catch (error) {
            console.error((error as Error).message);
        }
    };

    const onLoginButtonClick = async (event: React.FormEvent<HTMLFormElement>) => {
        event.preventDefault();
        if (!email || !password) return;

        try {
            const result = await useLogin(email, password);
            if (result.success && result.user) {
                // Sync user data with store
                const currentUser = auth.currentUser;
                if (!currentUser) {
                    const errorMessage = "An error occurred. Please try again.";
                    toast.error(errorMessage);
                    throw new Error(errorMessage);
                }

                useUser.getState().fetchUserData(currentUser.uid);
                useUser.getState().setUserEmail(currentUser.email);
                console.log("Forced user sync:", currentUser);
                console.log("Login successfull: ", result.user);
                router.push("/");
            } else throw new Error("An error occurred. Please try again.");
        } catch (error) {
            console.error((error as Error).message);
        } finally {
            setEmail("");
            setPassword("");
        }
    };

    useEffect(() => {
        if (user) router.push("/VideoPlayer");
    }, [user, router]);

    if (!isClient) return; // return loading spinner

    return (
        <>
            {isClient && (
                <div className="bg-cover bg-center bg-wavy-lines flex items-stretch bg-grey-lighter min-h-screen">
                    <div className="pt-20 pb-10 w-96 bg-transparent border-2 border-black/30 backdrop-blur-lg shadow-md text-black rounded-lg p-10 mx-auto my-28">
                        {/* Form */}
                        <Form
                            passwordIsVisible={passwordIsVisible}
                            email={email}
                            password={password}
                            confirmPassword={confirmPassword}
                            isLoginPage={isLoginPage}
                            onConfirmPasswordChange={onConfirmPasswordChange}
                            onEmailChange={onEmailChange}
                            onPasswordChange={onPasswordChange}
                            onLoginButtonClick={onLoginButtonClick}
                            onSignupButtonClick={handleSignupClick} // Custom handler
                            onGoogleIconClick={onGoogleIconClick}
                        />
                        <hr className="my-5 text-center text-gray-700" />
                        <ThirdPartyAuth fontSize="medium" onGoogleIconClick={onGoogleIconClick} /> {/* Third Party Authentication Icons */}
                        <ToggleForms isLoginPage={isLoginPage} /> {/* 'Don't have an account?' And 'Already have an account?' */}
                    </div>

                    {/* Modal for 6-digit Code Verification */}
                    {showCodeModal && (
                        <CodeModal code={code} email={email} handleCloseModal={handleCloseModal} handleVerify={handleVerify} setCode={setCode} />
                    )}

                    <div className="fixed bottom-8 left-8 z-50 w-auto max-w-xs">
                        <Toaster duration={5000} richColors /> {/* Sonner Notification */}
                    </div>
                </div>
            )}
        </>
    );
}
