import { toast } from "sonner";
import { useSignup } from "./index";

export default async function createNewClientUser(email: string, password: string, confirmPassword: string) {
    const result = await useSignup(email, password, confirmPassword);
    if (result.success) return result;

    switch (result.error) {
        case "auth/email-already-in-use":
            toast.error("Email is already in use.");
            break;
        case "auth/invalid-email":
            toast.error("Invalid email address.");
            break;
        case "auth/weak-password":
            toast.error("Weak password. Please choose a stronger password.");
            break;
        case "Password must be between 8 and 266 characters.":
            toast.error("Password must be between 8 and 266 characters.");
            break;
        case "Password must contain at least 2 letters.":
            toast.error("Password must contain at least 2 letters.");
            break;
        case "Password must contain at least 2 numbers.":
            toast.error("Password must contain at least 2 numbers.");
            break;
        case "Passwords do not match.":
            toast.error("Passwords do not match.");
            break;
        default:
            toast.error("An unknown error occurred.");
            break;
    }
}
