import { ICodeModal } from "@/app/utils";

export default function CodeModal({ handleCloseModal, handleVerify, email, setCode, code }: ICodeModal) {
    return (
        <div className="fixed inset-0 flex items-center justify-center bg-gray-500 bg-opacity-50">
            <div className="bg-white p-8 rounded-lg shadow-md max-w-sm w-full">
                <button
                    onClick={handleCloseModal}
                    className="ml-80 text-gray-500 text-2xl hover:text-teal-300 transform hover:scale-125 transition-transform duration-100"
                >
                    &times;
                </button>
                <h3 className="text-xl font-semibold text-center mb-4">Enter a 6-Digit Code to verify your Email</h3>
                <h6 className="mb-3"> You're almost there! We've sent a 6 digit code to {email}.</h6>
                <form onSubmit={handleVerify}>
                    {" "}
                    <input
                        type="text"
                        value={code}
                        onChange={(e) => setCode(e.target.value)}
                        maxLength={6}
                        className="w-full h-12 p-2 mb-4 border rounded-md border-gray-300 focus:outline-none focus:ring-2 focus:ring-teal-400"
                        placeholder="Enter Code"
                    />
                    <button
                        type="submit"
                        className={`w-full h-12 mt-4 bg-gradient-to-r from-gray-300 to-teal-400 text-white border rounded-lg cursor-pointer ${
                            code.length !== 6 ? "bg-gray-300 cursor-not-allowed" : "hover:from-orange-50 hover:to-pink-500"
                        }`}
                        disabled={code.length !== 6}
                    >
                        Verify
                    </button>
                </form>
            </div>
        </div>
    );
}
