import { toast } from "sonner";
import { Player } from "../interfaces";
import { addVideoToPlaylist } from "@/app/firebase";
import { User } from "firebase/auth";
import createVideoObject from "./createVideoObject";
import { containsVideo } from "./containsVideo";

export default async function processVideoAddition(
    buttonType: string,
    setCurrentVideo: (video: Player) => void,
    videos: Player[],
    addVideo: (video: Player) => void,
    user: User | null,
    clearQueue: () => void,
    togglePlaylist: (isActive: boolean) => void,
    duration: number,
    soundCloudVideo: Player,
    youtubeVideo: Player,
    tiktokVideo: Player,
    clearPlaylist: () => void
) {
    const video = createVideoObject(duration, tiktokVideo, youtubeVideo, soundCloudVideo);
    if (!video) return;

    if (containsVideo(videos, video)) return toast.error("Video is already in the playlist");

    if (user) {
        clearQueue();
        togglePlaylist(true);

        const result = await addVideoToPlaylist(user.uid, video);
        if (result.error) {
            if (result.error === "Video already exists in the playlist") {
                toast.error("Video already exists in the playlist");
            }
            return;
        }

        addVideo(video);
        setCurrentVideo(video);

        buttonType === "queue"
            ? toast.success("Video added to the queue")
            : buttonType === "playlist"
              ? toast.success("Video added to the playlist")
              : "";
    } else {
        clearPlaylist();
        togglePlaylist(false);

        addVideo(video);
        setCurrentVideo(video);

        buttonType === "queue"
            ? toast.success("Video added to the queue")
            : buttonType === "playlist"
              ? toast.success("Video added to the playlist")
              : "";
    }
}
