import GoogleIcon from "@mui/icons-material/Google";
import { IThirdPartyAuth } from "@/app/utils";

export default function ThirdPartyAuth({ onGoogleIconClick, fontSize }: IThirdPartyAuth) {
    return (
        <div className="flex justify-center">
            <GoogleIcon className="hover:cursor-pointer" fontSize={fontSize} onClick={onGoogleIconClick} />
        </div>
    );
}
