import Input from "./Input";
import { IPageModel } from "@/app/utils";
import VisibilityIcon from "@mui/icons-material/Visibility";
import AccountCircleIcon from "@mui/icons-material/AccountCircle";
import VisibilityOffIcon from "@mui/icons-material/VisibilityOff";

export default function PageModel({
    email,
    password,
    titleName,
    buttonName,
    isLoginPage,
    onClickEvent,
    onEmailChange,
    confirmPassword,
    onPasswordChange,
    emailPlaceholder,
    passwordIsVisible,
    passwordPlaceholder,
    onConfirmPasswordChange,
    confirmPasswordPlaceholder,
}: IPageModel) {
    // shared classnames
    const sharedDivClassName = "relative my-8 h-12 w-full";

    const sharedIconClassName = `absolute right-4 top-1/2 -translate-y-1/2 transform text-xl hover:text-pink-500 text-teal-400`;

    const sharedInputClassName = `h-full w-full rounded-full border-2 border-gray-200 bg-transparent px-6 py-2 text-black 
    placeholder-black outline-none`;

    return (
        <form onSubmit={onClickEvent}>
            {/* Title */}
            <h1 className="text-4xl text-center ">{titleName}</h1>

            {/* Email Field */}
            <Input
                type="text"
                value={email}
                iconSize="small"
                autoComplete="off"
                onChange={onEmailChange}
                Icon={AccountCircleIcon}
                placeholder={emailPlaceholder}
                divClassName={sharedDivClassName}
                iconClassName={sharedIconClassName}
                inputClassName={sharedInputClassName}
            />

            {/* Password Field */}
            <Input
                type="password"
                iconSize="small"
                value={password}
                autoComplete="off"
                onChange={onPasswordChange}
                divClassName={sharedDivClassName}
                iconClassName={sharedIconClassName}
                inputClassName={sharedInputClassName}
                placeholder={passwordPlaceholder}
                Icon={passwordIsVisible ? VisibilityIcon : VisibilityOffIcon}
            />

            {/* Confirm Password Field -- Only for Sign Up Route */}
            {!isLoginPage && (
                <Input
                    iconSize="small"
                    type="password"
                    autoComplete="off"
                    value={confirmPassword}
                    divClassName={sharedDivClassName}
                    onChange={onConfirmPasswordChange}
                    iconClassName={sharedIconClassName}
                    inputClassName={sharedInputClassName}
                    placeholder={confirmPasswordPlaceholder}
                    Icon={passwordIsVisible ? VisibilityIcon : VisibilityOffIcon}
                />
            )}

            {/* Log in or Sign up Button --Depends on route*/}
            <button
                type="submit"
                className="h-12 w-full rounded-full shadow-md hover:shadow-lg 
                    bg-gradient-to-r from-gray-300 to-teal-400 text-white border 
                    cursor-pointer hover:from-orange-50 hover:to-pink-500"
            >
                {buttonName}
            </button>
        </form>
    );
}
