import { IInput } from "@/app/utils";

export default function Input({
    Icon,
    type,
    value,
    iconSize,
    onChange,
    placeholder,
    divClassName,
    autoComplete,
    iconClassName,
    inputClassName,
}: IInput) {
    return (
        <div className={divClassName}>
            <input
                required
                type={type}
                value={value}
                onChange={onChange}
                placeholder={placeholder}
                className={inputClassName}
                autoComplete={autoComplete}
            />
            <Icon fontSize={iconSize} className={iconClassName} />
        </div>
    );
}
